.example {
  background-color: #EAEAEA;
  @media (min-width: $tablet-width) {
    background: linear-gradient( #ffffff 609px, #EAEAEA 609px);
  }
  @media (min-width: $desktop-width) {
    background: linear-gradient( #ffffff 179px, #EAEAEA 179px);
  }
}

.example__wrapper {
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 25px;
  padding-bottom: 43px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  @media (min-width: $tablet-width) {
    padding-top: 43px;
  }
  @media (min-width: $desktop-width) {
    padding-top: 0;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 65px;
  }
}

.example-column {
  @media (min-width: $desktop-width) {
    width: 436px;
  }
}

.example__title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin-bottom: 40px;
  @media (min-width: $tablet-width) {
    text-align: left;
  }
  @media (min-width: $desktop-width) {
    padding-top: 43px;
    width: 436px;
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 146px;
  }
}

.example__description {
  font-family: $font-family-extra;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 20px;
  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
  @media (min-width: $desktop-width) {
    width: 436px;
    margin-bottom: 69px;
  }
}

.example__resources {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 80px;
  }
  @media (min-width: $desktop-width) {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0;
  }
}

.example__list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 124px 124px;
  gap: 32px;
  margin-bottom: 20px;
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 55px;
  }
}

.example__item {
  position: relative;
  border: 1px solid #CDCDCD;
  min-height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 14px;
  @media (min-width: $tablet-width) {
    min-height: 77px;
  }
}

.example__number {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 11px;
  }
}

.example__parametr {
  margin: 0;
  padding: 0;
  position: absolute;
  background-color: #EAEAEA;
  width: 77px;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  text-align: center;
  font-family: $font-family-extra;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  @media (min-width: $tablet-width) {
    background-color: #ffffff;
    font-size: 14px;
    line-height: 14px;
  }
  @media (min-width: $desktop-width) {
    background-color: #EAEAEA;
  }
}

.example__price {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  display: flex;
  flex-wrap: wrap;
  @media (min-width: $tablet-width) {
    font-size: 20px;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 0;
  }
  @media (min-width: $desktop-width) {
    flex-direction: row;
    align-items: center;
  }

  &-text {
    margin-right: 3px;
    @media (min-width: $tablet-width) {
      margin-right: 0;
      margin-bottom: 9px;
    }
    @media (min-width: $desktop-width) {
      margin-right: 63px;
      margin-bottom: 0;
    }
  }
}

.example__img {
  display: none;
  object-fit: contain;
  margin-bottom: 15px;
  &--mobile {
    &-active {
      display: block;
      margin: 0 auto;
      @media (min-width: $tablet-width) {
        display: none;
      }
    }
  }
  &--tablet {
    display: none;
    &-active {
      @media (min-width: $tablet-width) {
        display: block;
        margin: 0 auto;
      }
    }
  }
  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
  }
}

.example__range {
  margin: 0 auto;
}


